
export default{
  install(Vue,options){
  	 //判断环境
  	 Vue.prototype.utilIsLocalhost = function(){
  	 	var k_host = window.location.host;
  	 	return"localhost:8080"==k_host||"htmlpreview.github.io"==k_host
//		if(k_host=="localhost:8080"){
//			return true;
//		}else if(k_host=="htmlpreview.github.io"){
//			return true;
//		} else{
//			return false;
//	    }
  	 };
  	 //提示消息
  	 Vue.prototype.utilMsg = function(msgs){
  	  	var msgText = $("#msgText").text();
  	  	if(!msgText){
  	  		var html = '<div id="msgText">'+msgs+'</div>'
  	  		$("body").append(html);
  	  	}else{
  	  		$("#msgText").text(msgs);
  	  	}
  	  	$("#msgText").fadeIn();
  	  	setTimeout(function(){
  	  		$("#msgText").fadeOut();
  	  	},3000)
  	 };
  }
}


